.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.container {
  align-items: center;
  background: -webkit-linear-gradient(to left,rgb(60, 133, 173), rgb(144, 210, 231)); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(to left,rgb(60, 133, 173), rgb(144, 210, 231)); /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(to left,rgb(60, 133, 173), rgb(144, 210, 231)); /* For Firefox 3.6 to 15 */
  background: linear-gradient(to left,rgb(60, 133, 173), rgb(144, 210, 231)); /* Standard syntax (must be last) */
  display: flex;
  height: 100vh;
  justify-content: center;
  max-width: 100vw !important;
}
